import React, { useEffect } from "react";

import { useParams } from "react-router-dom";

import { Link } from "react-router-dom";
import { CCard, CCardBody, CCol, CCardHeader, CImage, CCardSubtitle, CButton, CRow, CCardTitle } from "@coreui/react";
import axios from "axios";
import { useState } from "react";

const ViewUser = () => {
  const { id } = useParams();
  const token =
    typeof window !== "undefined" ? localStorage.getItem("status") : null;
  

  const [mydata,setMydata] = useState([]);
  console.log("single_profile", id);
  useEffect(() => {
    axios
      .get("userDetails/" + id, {
        headers: {
          Accept: "application/json",
          Authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        console.log("single_profile", res);
        setMydata(res.data.data);
        // res.data.status == 200 ?   setMydata(res.data.data):  setMydata([])
       
      });
  }, [id, token]);

  return (
    <>
      <div className="container-fluid ">
        <div className="row">
          <div className="col-md-2  p-0">
            <div className="sidebar-open"></div>
          </div>
          <div className="col-md-12 pl-60">
            <div className="row mt-4 px-2 ">
              <div className="px-2">
                <CCol md={12}>
                  <CCard className="mb-4">
                    <CCardHeader>
                      <strong>View User Details</strong>
                    </CCardHeader>
                    <CCardBody>
                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Profile :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          {mydata?.profile_img == "" ? (
                            <span style={{ color: "red" }}>
                              No Profile Image
                            </span>
                          ) : (
                            <Link to={mydata?.profile_img} target="_blank">
                              {" "}
                              <CImage
                                // align="center"
                                width={75}
                                rounded
                                thumbnail
                                orientation="top"
                                src={mydata?.profile_img}
                              />{" "}
                            </Link>
                          )}
                        </CCol>
                      </CCol>


                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Name :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">{mydata?.name}</span>
                        </CCol>
                      </CCol>
                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>User Name :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>{mydata?.user_name}</CCol>
                      </CCol>
                     
                      
                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Mobile Number :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>{mydata?.phone}</CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Gender :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}> {mydata?.gender == "1" ? "Male" : mydata?.gender == "2" ? "Female" :"Other" }</CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>City :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">{mydata?.city}</span>
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Call/Chat History :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                        <Link
                          // onClick={open}
                          to={`/chat-history/${id}`}
                          className="font-weight-bold font-xs text-medium-emphasis"
                        >
                          <CButton className="text-white" color="dark">
                            View 
                          </CButton>
                        </Link>
                        </CCol>
                      </CCol>
                      
                    </CCardBody>
                  </CCard>
                </CCol>



                {/* <CCol md={6}>
                  <CCard className="mb-4">
                    <CCardHeader>
                      <CCol md={12} className="d-flex justify-content-between ">
                        <strong className="mt-2">Call History</strong>

                        <Link
                          // onClick={open}
                          to="/call-history"
                          className="font-weight-bold font-xs text-medium-emphasis"
                        >
                          <CButton className="text-white" color="dark">
                            View More
                          </CButton>
                        </Link>
                      </CCol>
                    </CCardHeader>
                    
                  </CCard>
                </CCol>
            */}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ViewUser;
